       class=\"errMsg\"\r\n                        >{{ errorSubmit.password[0] }}</span\r\n                    >\r\n                </label>\r\n                <label class=\"form-grp-cstm\">\r\n                    Confirm Password\r\n                    <input name=\"password_confirmation\" type=\"password\" />\r\n                </label>\r\n                <button type=\"submit\">Reset Password</button>\r\n            </form>\r\n        </div>\r\n    </div>\r\n</template>\r\n\r\n<script>\r\nexport default {\r\n    props: [\"errorSubmit\", \"oldvalues\", \"url\", \"mail\", \"token\"],\r\n    data() {\r\n        return {\r\n            active: this.url ?? \"Host\",\r\n            csrf: $('meta[name=\"csrf-token\"]').attr(\"content\"),\r\n            email: this.mail ?? this.oldvalues[\"email\"] ?? null,\r\n        };\r\n    },\r\n    computed: {\r\n        formSubmitRoute() {\r\n            if (\r\n                this.url == \"host\" ||\r\n                this.url == \"bride\" ||\r\n                this.url == \"groom\" ||\r\n                this.url == \"member\"\r\n            ) {\r\n                return route(\"hostpassword.update\");\r\n            }\r\n            if (this.url == \"vendor\") {\r\n                return route(\"vendorpassword.update\");\r\n            }\r\n            if (this.url == \"guest\") {\r\n                return route(\"guestpassword.update\");\r\n            }\r\n            if (this.url == \"company\") {\r\n                return route(\"companypassword.update\");\r\n            }\r\n            if (this.url == \"admin\") {\r\n                return route(\"adminpassword.update\");\r\n            }\r\n        },\r\n    },\r\n};\r\n</script>\r\n\r\n<style scoped>\r\n.main-cont {\r\n    width: 100%;\r\n    height: 100%;\r\n    min-height: 80vh;\r\n    display: flex;\r\n    justify-content: center;\r\n    align-items: center;\r\n}\r\n.verify-box {\r\n    background: #ffffff;\r\n    border: 0.5px solid #e1e1e1;\r\n    border-radius: 16px;\r\n    max-width: 532px;\r\n    margin: 10px;\r\n    padding: 49px;\r\n}\r\n.verify-box > .top {\r\n    display: flex;\r\n}\r\n.top h1 {\r\n    font-family: \"Poppins\";\r\n    font-style: normal;\r\n    font-weight: 500;\r\n    font-size: 25px;\r\n    line-height: 38px;\r\n    color: #000000;\r\n}\r\n.top p {\r\n    font-family: \"Poppins\";\r\n    font-style: normal;\r\n    font-weight: 400;\r\n    font-size: 16px;\r\n    line-height: 24px;\r\n    color: #000000;\r\n}\r\n.top img {\r\n    width: 100px;\r\n    height: 100px;\r\n    align-self: flex-end;\r\n}\r\n.middle {\r\n    background: #e8f8e2;\r\n    border-radius: 5px;\r\n    padding: 16px;\r\n    font-family: \"Inter\";\r\n    font-style: normal;\r\n    font-weight: 400;\r\n    font-size: 15px;\r\n    line-height: 20px;\r\n    color: #3b682c;\r\n}\r\n.middle > a {\r\n    text-decoration: none;\r\n    display: block;\r\n    width: 100%;\r\n    color: inherit;\r\n}\r\n.form-grp-cstm {\r\n    font-family: \"Inter\";\r\n    font-style: normal;\r\n    font-weight: 500;\r\n    font-size: 14px;\r\n    line-height: 20px;\r\n    color: #6b7280;\r\n    display: flex;\r\n    flex-direction: column;\r\n}\r\n.form-grp-cstm input {\r\n    background: #ffffff !important;\r\n    border: 0.896226px solid #e5e7eb !important;\r\n    box-shadow: 0px 0.896226px 1.79245px rgba(31, 41, 55, 0.08) !important;\r\n    border-radius: 3.58491px !important;\r\n    padding: 15px 7px;\r\n    margin-bottom: 5px;\r\n}\r\n.verify-box > form {\r\n    margin-top: 15px;\r\n}\r\n.verify-box > form > button {\r\n    font-family: \"Inter\";\r\n    font-style: normal;\r\n    font-weight: 400;\r\n    font-size: 13.3726px;\r\n    line-height: 19px;\r\n    color: #ffffff;\r\n    width: 162px;\r\n    height: 40.5px;\r\n    background: #461952;\r\n    border-radius: 6.39474px;\r\n    display: flex;\r\n    justify-content: center;\r\n    align-items: center;\r\n    margin-left: auto;\r\n    margin-top: 12px;\r\n}\r\n</style>\r\n"],"sourceRoot":""}]);
